import React from "react"
import Preloader from "../../common/Preloader/Preloader"
import classes from "./profileInfo.module.css"
import ProfileStatusWithHooks from "./ProfileStatusWithHooks"

const ProfileInfo = ({ profile, status, updateStatus, isOwner, savePhoto }) => {
  if (!profile) {
    return <Preloader />
  }
  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      savePhoto(e.target.files[0])
    }
  }
  return (
    <div>
      <div className={classes.descriptionBlock}>
        <img
          className={classes.image}
          src={
            profile.photos.large ||
            "https://cdn.pixabay.com/photo/2016/08/08/09/17/avatar-1577909_1280.png"
          }
        />
        {isOwner && <input type="file" onChange={onMainPhotoSelected} />}
        <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />
        <div>
          {" "}
          <span className={classes.bold}>About Me: </span>{" "}
          <span>{profile.aboutMe}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Facebook: </span>{" "}
          <span>{profile.contacts.facebook}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Github: </span>{" "}
          <span>{profile.contacts.github}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Instagram: </span>{" "}
          <span>{profile.contacts.instagram}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Main Link: </span>{" "}
          <span>{profile.contacts.mainLink}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Twitter: </span>{" "}
          <span>{profile.contacts.twitter}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>VK: </span>{" "}
          <span>{profile.contacts.vk}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Website: </span>{" "}
          <span>{profile.contacts.website}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Youtube: </span>{" "}
          <span>{profile.contacts.youtube}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Full Name: </span>
          <span>{profile.fullName}</span>{" "}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>Looking for a job: </span>{" "}
          {profile.lookingForAJob ? <span>True</span> : <span>False</span>}
        </div>
        <div>
          {" "}
          <span className={classes.bold}>
            Looking for a job description:{" "}
          </span>{" "}
          <span>{profile.lookingForAJobDescription}</span>{" "}
        </div>
      </div>
    </div>
  )
}

export default ProfileInfo
