import React from "react"
import classes from "./post.module.css"

const Post = (props) => {
  return (
    <div className={classes.item}>
      <img src="https://img.anews.com/media/gallery/125000070/135413263.jpg" />
      {props.message}
      <div>
        <span>like </span>
        {props.likes}
      </div>
    </div>
  )
}

export default Post
